@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@400;600&family=Estonia&display=swap);
body {
  margin: 0;
  font-family: 'Dosis', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  background-color: #dfdfe0;
  min-height: 100vh;
  padding: 2rem;
}

.search-input{
  max-width: 350px;
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid white;
  box-shadow: 0 1px 3px 3px rgb(141, 141, 141);
  margin-bottom: 20px;
}
.contact{
    max-width: 350px;
    background-color: white;
    border-radius: 10px;
    margin-bottom: 30px;
    padding: 20px;
    box-shadow: 0 3px 5px 5px lightgrey;
}

.contact h3{
    font-family: 'Estonia', cursive;
    font-weight: 600;
    margin-bottom: 10px;
}

.contact p{
    font-weight: 400;
    margin-bottom: 10px;
}
